      h1 Patronaje masculino bsico

    .row.justify-content-center.caja-gris1.g-0.mb-5
      .col-lg.mb-lg-0.mb-5
        p.mb-0.px-5.py-4 Una de las fases ms importantes del proceso de desarrollo de colecciones de moda, tiene que ver con el patronaje, pues ac es donde se comienzan a materializar las ideas de diseo a partir de la construccin en plano de cada una de las piezas que van a conformar una prenda. Este proceso lleva todo un anlisis previo de la manera en la que se entiende el cuerpo y cmo garantizar la funcionalidad y vestibilidad de las prendas.
          br
          br
          | Los patrones en la moda son lo que los planos a la arquitectura, pues a travs del uso de dibujos planos y geomtricos se traduce la volumetra del cuerpo humano y se establece la construccin y desarrollo de las prendas.
      .col-lg-auto
        figure
          img(src="@/assets/curso/tema2-imagen1.jpg")

    #t_2_1.titulo-segundo
      h2 2.1    Induccin al patronaje y escalado masculino

    .row.justify-content-center.mb-4
      .col-lg-4.mb-lg-0.mb-5
        figure
          img(src="@/assets/curso/tema2-imagen2.jpg")
      .col-lg-8
        p.mb-0 El patronaje masculino tiene sus orgenes en la sastrera. Desde los siglos XVIII y XIX, donde el hombre pas del glamour excesivo a la sobriedad del traje que se us hasta mediados del siglo XX, momento en el que se diversifica la moda en general con la aparicin de los universos del vestuario; sin embargo, el desarrollo de las prendas de vestir masculinas sigue partiendo de los mismos patrones bsicos y es al momento de realizar los desarrollos cuando se ajustan las teoras segn el diseo a construir para conseguir el resultado esperado.
          br
          br
          | Durante el desarrollo de esta unidad se tendrn como bases lo patrones de la camisa, el pantaln, la chaqueta y la camiseta de los cuales, al realizar las variaciones pertinentes, es posible obtener los diferentes desarrollos de prendas exteriores.

    .cajon.color-primario.p-5.mb-4
      p.mb-0 Para la obtencin de las diferentes tallas se utiliza una tcnica conocida como escalado, la cual permite incrementar o disminuir la talla de un patrn o molde a partir de la modificacin de ciertas medidas en puntos especficos partiendo de la diferencia entre tallas que se obtiene de la tabla de medidas con la que se trabaje.

    p.mb-5 Las teoras de escalado funcionan de igual manera para las diferentes lneas de vestir, lo que cambia son las frmulas de escalado, ya que, para cada tipologa de prenda, segn la lnea y el cuadro de tallas a trabajar, se emplean valores diferentes.

    .row.justify-content-center
      .col-lg-10
        figure.mb-5
          img(src="@/assets/curso/tema2-imagen3.jpg")
        p.mb-5 Entender el cuerpo como un cilindro para el desarrollo de patrones partiendo de los cuartos de medidas, ser un elemento indispensable para el proceso de escalado, pues al trasladar la volumetra al dibujo plano, se establecen ciertos ejes que harn las veces de centros de escalado y sern puntos fijos que no se modificarn.
        figure.mb-5
          img(src="@/assets/curso/tema2-imagen4.svg").mb-2
          figcaption Fuente: elaboracin propia en Optitex.

    .row.justify-content-center.mb-5
      .col-lg-6
        .caja.temporal--rosa.p-5.mb-5
          p.mb-0 El escalado parte de unos ejes, que para las prendas superiores, sern las lneas de centro del frente y el centro espalda como ejes verticales, y como eje horizontal se emplea la lnea o altura de sisa. El eje se comporta como centro del escalado, mantenindose inmvil.
        figure
          img(src="@/assets/curso/tema2-imagen5.svg").mb-3
          figcaption Fuente: Escalado (Gaviria; 2018).
      .col-lg-6
        .caja.temporal--rosa.p-5.mb-5
          p.mb-0 En las partes inferiores masculinas (pantalones) el eje vertical se ubica en la lnea de aplome y el eje horizontal a la altura de cadera o tiro. Creciendo y decreciendo horizontalmente hacia los costados, tiro y entrepierna y verticalmente hacia la cintura y la bota.
        figure
          img(src="@/assets/curso/tema2-imagen6.svg").mb-3
          figcaption Fuente: Escalado (Gaviria; 2018)

    p.mb-5 Para escalar es muy importante tener en cuenta estos ejes y sobre estos trazar un plano cartesiano en cada punto a escalar, para luego aplicar la medida (segn incrementos en tabla de medidas) hacia afuera (en tallas mayores) y hacia adentro (en tallas menores), manteniendo las proporciones y curvas del patrn base o inicial.

    .row.justify-content-center
      .col-lg-5.d-flex
        p.mb-0.caja--outline-rosa.px-4.py-3.texto-rosa Posicin de las escuadras para trazar los planos cartesianos en puntos de prendas superiores.
      .col-lg-5.d-flex
        p.mb-0.caja--outline-rosa.px-4.py-3.texto-rosa Posicin de las escuadras para trazar los planos cartesianos en puntos de prendas inferiores.

    .row.justify-content-center
      .col-lg-5.d-flex
        figure.mb-4
