          figcaption Fuente: Escalado (Gaviria; 2018)
      .col-lg-10
        .cajon.color-acento-contenido.p-5.mb-4
          p.mb-0 A continuacin se presenta un ejemplo en el que se explica grficamente el escalado del punto de hombro por sisa (hombro x sisa), con unos valores de 0.84cm en X y 0.76cm en Y (los cuales dependen de cada punto y de la tabla de medidas con la que se trabaje), como se ve en la figura.
        figure.mb-4
          img(src="@/assets/curso/tema2-imagen9.svg").mb-3
          figcaption Fuente: Escalado (Gaviria; 2018). Ejemplo de escalado del punto hombro por sisa.
        p.mb-4 Como se observa en la figura anterior, se marc el plano cartesiano sobre el punto y en cada uno de los ejes la medida correspondiente (0.84 cm en X y 0.76 cm en Y), para luego prolongar paralelamente estas lneas formando una retcula o cuadrcula, en la que se identifican cada uno de los puntos de cruces de las tallas. Teniendo como referencias cada uno de estos puntos se traza cada talla utilizando el molde base.
        p.mb-5.text-center.py-3.caja.temporal--rosa Adaptado de Carrera de Diseo y Gestin en Moda, 2015; Gaviria; 2018.
