    #t_2_4.titulo-segundo
      h2 2.4    Variaciones de la camisa: diferentes tipos de cortes, recogidos y almillas alforzas

    .cajon.color-primario.p-5.mb-4
      p.mb-0 Para desarrollar diferentes tipos de camisas se hace necesario modificar los moldes bsicos, dependiendo de las especificaciones del diseo o ficha tcnica. A continuacin, se presentan algunas de las ms comunes.

