      .col-lg-6.mb-4
        p.mb-4.fw-bold.texto-rosa Cortes
        p.mb-4 Es fundamental tener en cuenta que siempre que se realice un corte a una pieza se le debe adicionar la margen de costura para poder confeccionar (unir) ambas piezas en la tela.
        .titulo-sexto.color-acento-contenido
          span Ejemplo de corte y costuras.
        figure.mb-4
          img(src="@/assets/curso/tema2-imagen70.svg").mb-2
          figcaption Fuente: elaboracin propia en Optitex.

      .col-lg-9.mb-4
        .cajon.color-acento-contenido.p-5.mb-4
          p.mb-0 Recogidos: si al hacer un corte llevar un recogido, a la pieza que se va a recoger se le deber aumentar la medida para dicho recogido, en la zona en que se ha de recoger y marcarse con piquetes tanto en la pieza que se recoge, como en la pieza en la que se une, como gua de confeccin.

      .col-lg-7.mb-4
        SlyderC(:datos="datosSlyder7")


    p.mb-5
      strong.texto-rosa.fw-bold Almilla
      br
      br
      | La almilla o canes, es una pieza (corte) en la parte superior de la camisa, la ms comn es la almilla de espalda, sin embargo, tambin se hace en ocasiones en el frente. La almilla sirve como refuerzo de los hombros, por lo tanto, va doble (se corta dos veces por talla) y se confecciona encarterada.
      br
      br
      | Para el desarrollo de la almilla se traza una lnea perpendicular al centro de la espalda, sta se traza segn diseo, generalmente a 11 cm o 12 cm.

    .row.justify-content-center
