          .col-lg-6.d-flex
            .caja.temporal--rosa.mb-5.p-5
              p.mb-0 Con el fin de que la camisa se ajuste mejor a la espalda, se marca una pinza (curva) con un ancho igual a la altura de escote delantero dividido entre seis (6) y de largo mitad de lnea de almilla menos 2 cm y se descuenta a la espalda, as:
          .col-lg-6.d-flex
            .caja.temporal--rosa.mb-5.p-5
              p.mb-0 Tanto la almilla como la espalda van abiertas por espejo en la lnea de centro espalda. Al ser un corte que se realiza, se deben dar mrgenes de costura.
        .row.justify-content-center
          .col-lg-6
            .titulo-sexto.color-acento-contenido
              span Almilla.
            figure.mb-4
