              .row.justify-content-center.align-items-center.background1-especial2.border-radius.left.right.mb-5
                .col-auto.background1-especial1.border-radius.right
                  figure.p-4
                    img(src="@/assets/curso/tema1-imagen63.svg")
                .col
                  p.mb-0 Nota: la almilla se corta al travs de la tela, es decir, el aplome se marca perpendicular a la lnea de centro.

    p.mb-5
      strong.texto-rosa.fw-bold Alforzas
      br
      br
      | Son una serie de pliegues planos cosidos secuencialmente en la tela, que se vuelcan hacia un lado, las hay de todas las medidas. Para realizar las alforzas se deben marcar las lneas por las cuales se abrir la pieza, para el ejemplo se harn en el frente cuatro (4) alforzas paralelas al centro frente, con una separacin de 1.5 cm, la primera a 4 cm del punto hombro por escote, as:

    .row.justify-content-center
      .col-lg-8.mb-5
        .titulo-sexto.color-acento-contenido
          span Alforzas
        figure.mb-4
          img(src="@/assets/curso/tema2-imagen78.svg")
          figcaption Fuente: elaboracin propia en Optitex.
        .cajon.color-acento-contenido.p-5.mb-5
          p.mb-0 Se corta la pieza por las lneas de las alforzas y se ubican sobre una nueva pieza de papel con una lnea horizontal que servir de gua (para el ejemplo en el ruedo), separando cada una de las piezas el ancho deseado de la alforza por dos (se hace doble para que la tela forme el prense o pliegue de la alforza) y marcando la mitad de este espacio. Para el ejemplo se harn las alforzas de 0.5 cm, por lo que se separarn 1 cm.
        .titulo-sexto.color-acento-contenido
          span Alforzas.
        figure
          img(src="@/assets/curso/tema2-imagen85.svg")
          figcaption Fuente: elaboracin propia en Optitex.
