    #t_2_5.titulo-segundo
      h2 2.5    Cuellos, puos perillas, tapas y charreteras

    p.mb-5 As como la silueta, los cortes y recogidos modifican un molde, es fundamental entender que los cuellos, puos, perillas, tapas y charreteras intervienen en la creacin de mltiples diseos a partir de la camisa bsica, su modificacin da como resultado un modelo diferente.

    .row.justify-content-center
      .col-lg-10
        .row.justify-content-center.mb-5
          .col-lg-4.mb-lg-0.mb-5
            figure
              img(src="@/assets/curso/tema2-imagen88.jpg")
          .col-lg-8
            .row
              .col-auto
                div.mb-4.px-5.py-2.caja-titulo-bordes-especial.w-auto.fw-bold Cuellos
            p.mb-0 Son la pieza que rodea el cuello en las prendas superiores. Su funcin es abrigar y proteger dicha parte del cuerpo. Con las variaciones en su diseo se busca aportar una funcin esttica a la prenda.
              br
              br
              | El tipo de cuello tambin variar dependiendo de la finalidad de la camisa, es decir, depende del diseo de este se recomienda el uso de otros accesorios como corbatas o corbatines segn la ocasin de uso para la que fue pensada la prenda.
              br
              br
              | En funcin de la moda y de la prenda, el cuello ha adoptado numerosas variaciones de forma a lo largo de la historia. En la indumentaria masculina, los cuellos camiseros se han popularizado para el resto de las prendas como abrigos o chaquetas en que va unido a la solapa.
              br
              br
              | El trazo de estos cuellos est basado en el patrn base dado en la camisa bsica.
              br
              br
              | En camisas, se distinguen los siguientes tipos de cuellos:
        .row.mb-4.align-items-center
          .col-lg-auto
            .circle
          .col-lg-auto
            p.mb-0.fw-bold.texto-rosa Cuello ingls
        p.mb-5 Es el tradicional de las camisas de vestir, lo cual tiene sentido si se toma en cuenta que la sastrera naci en Inglaterra.  Sus puntas son alargadas, aunque el tamao puede variar dependiendo del vestido sastre.
          br
          br
          | La altura de este cuello suele llegar un poco ms arriba, por lo que s est cerrado da la sensacin de estar ms arreglado; por el tamao de las puntas, no es recomendable usarlo abierto ya que puede dar un efecto de avin.  Cuando se usa con corbata, el nudo debe ser ms estrecho por el espacio que hay y puede lucir un poco menos.
        .row.mb-4.align-items-center
          .col-lg-auto
            .circle
          .col-lg-auto
            p.mb-0.fw-bold.texto-rosa Cuello americano
        .row.justify-content-center.mb-5
          .col-lg-7.mb-lg-0.mb-5
            p.mb-0 Este tipo de cuello tiene su origen en Inglaterra, concretamente en el deporte del polo, donde se decidi por abotonar las palas de los polos (alas del cuello) para que estas no golpearan la cara del jinete al cabalgar. Sin embargo, fue en Estados Unidos donde se populariz esta solucin y se llev fuera del mundo del deporte. Suele tener botones en las puntas.
          .col-lg-5
            figure
              img(src="@/assets/curso/tema2-imagen89.svg")
        .row.justify-content-center.mb-5
          .col-lg-5
            figure
              img(src="@/assets/curso/tema2-imagen90.svg")
          .col-lg-7.mb-lg-0.mb-5
            .row.mb-4.align-items-center
              .col-lg-auto
                .circle
              .col-lg-auto
                p.mb-0.fw-bold.texto-rosa Cuello francs
            p.mb-0 Se caracteriza porque las puntas en la parte superior estn muy juntas y abajo suelen ser anchas, para permitir el uso corbatas con nudos grandes.
        .row.mb-4.align-items-center
          .col-lg-auto
            .circle
          .col-lg-auto
            p.mb-0.fw-bold.texto-rosa Cuello de <i>smoking</i>, <i>opera</i> o <i>wing collar</i>
        .row.justify-content-center.mb-5
          .col-lg-9.mb-lg-0.mb-5
            p.mb-0 Se caracteriza por tener picos pequeos, separados y muy abiertos donde las palas(alas) son muy pequeas. El objetivo es no restar ni un centmetro de protagonismo a la pajarita o corbatn, la cual se debe llevar s o s.
              br
              br
              | Para el trazo del cuello de <i>Smoking</i> se debe dibujar un rectngulo de 11 x 22 cm. Paralelas a la lnea horizontal, trazar dos lneas ms de 5 y 3 cm. de separacin, quedando el rectngulo dividido en 3 secciones, una de 5 cm, y dos de 3 cm.
          .col-lg-3
            figure
              img(src="@/assets/curso/tema2-imagen91.svg")
      .col-lg-8
        ol.lista-ol.lista-ol--separador.color-primario.pt-3.mb-5
          li
            p.mb-0.w-100.text-center En la primera lnea horizontal superior, se marca un punto a 14 cm. dividindola en dos partes.
          li
            p.mb-0.w-100.text-center La segunda lnea se divide en 4 puntos: a 11,5 cm, luego a 5 cm. luego a 3.
          li
            p.mb-0.w-100.text-center Lo que resta es unir los puntos marcados como se muestra en la imagen.
      .col-lg-10
        figure.mb-5
          img(src="@/assets/curso/tema2-imagen92.svg")
      .col-lg-8
        .row.justify-content-center.mb-5
          .col-lg-3
            figure
              img(src="@/assets/curso/tema2-imagen93.svg")
          .col-lg-9.mb-lg-0.mb-5
            .row.mb-4.align-items-center
              .col-lg-auto
                .circle
              .col-lg-auto
                p.mb-0.fw-bold.texto-rosa Cuello francs
            p.mb-0 Tambin llamado Cuello Club (o vintage). Es poco comn e informal se distingue por las puntas del ala del cuello redondeadas. Se usa sin corbata
        .row.justify-content-center.mb-5
          .col-lg-9.mb-lg-0.mb-5
            .row.mb-4.align-items-center
              .col-lg-auto
                .circle
              .col-lg-auto
                p.mb-0.fw-bold.texto-rosa Cuello italiano
            p.mb-0 Se conoce en ingls como spread collar (o cuello ancho), es una pieza ms abierta y con puntas un poco ms cortas que el ingls. Se caracteriza porque las puntas de este tipo de camisa estn ms separadas. Ideal para usar con un nudo gruesos como el doble o el Windsor. No se debe usar sin corbata.
          .col-lg-3
            figure
              img(src="@/assets/curso/tema2-imagen94.svg")
        .row.justify-content-center.mb-5
          .col-lg-3
            figure
              img(src="@/assets/curso/tema2-imagen95.svg")
          .col-lg-9.mb-lg-0.mb-5
            .row.mb-4.align-items-center
              .col-lg-auto
                .circle
              .col-lg-auto
                p.mb-0.fw-bold.texto-rosa Cuello mao
            p.mb-0 Es de origen chino sube entre 2 y 5 cm por la base del cuello, no tiene doblez, sus bordes pueden ser rectos o redondeados. Se caracteriza por su poca altura y sobre todo por no tener palas o alas de cuello.

    .row
      .col-auto
        div.mb-4.px-5.py-2.caja-titulo-bordes-especial.w-auto.fw-bold Puos de la camisa

    p.mb-5 Los puos como complementos de las camisas pueden variar el diseo y adems indicar la ocasin de uso de la misma. Ejemplo: en Inglaterra la alternativa al puo doble suele ser un puo con dos botones. En cambio, los italianos, optan por el clsico puo con un botn o por el puo con corte tambin con un slo botn.

    figure.mb-5
      img(src="@/assets/curso/tema2-imagen96.jpg")

    .row.justify-content-center.mb-5
      .col-lg-8.mb-lg-0.mb-5
        figure.mb-5
          img(src="@/assets/curso/tema2-imagen97.svg")
          figcaption Fuente: i.pinimg.com/originals, (s.f.).
        p.mb-5 En el trabajo de diseo es vlido aadir otro tipo de dobleces, cortes y algunas piezas como sucede en el modelo italiano o con botn anudado. Todos estos modelos son para elaborar camisas sobre medida muy utilizado en sastrera y en vestuario de fiesta y gala. Aqu el ojal del puo se puede dividir en dos tipos, en ojal para botn, y el ojal para gemelos. Mucho ms formal el segundo, y el nico indicado para camisas que vayan a llevarse con un traje de etiqueta.
        figure.mb-3
          img(src="@/assets/curso/tema2-imagen98.svg")
      .col-lg-6.mb-lg-0.mb-5
        p.mb-4.text-center.py-2.px-5.caja.temporal--rosa En cuanto al corte del puo, se tienen tres modelos principalmente:
      .col-lg-8.mb-lg-0.mb-5
        ol.lista-ol--cuadro.lista-ol--separador.list-with--borders.py-4.mb-5
          li
            .lista-ol--cuadro__vineta.circle.color-acento-contenido
              span 1
            | Corte recto o ingls. Los extremos del puo se unen formando una lnea horizontal.
          li
            .lista-ol--cuadro__vineta.circle.color-acento-contenido
              span 2
            | Corte redondeado o americano. Los extremos del puo se meten hacia dentro con forma redondeada.
          li
            .lista-ol--cuadro__vineta.circle.color-acento-contenido
              span 3
            | Corte italiano o sesgado. Los extremos del puo se meten hacia dentro formando un pico.
        .cajon.color-primario.p-5.mb-5
          p.mb-0 Y ya siendo de puo ms elegante y formal se encuentra el puo doble o francs: va doblado hacia dentro y consta de 4 ojales, que debemos hacer coincidir dos a dos para colocar entre ellos el gemelo. Se usa si se viste chaqu, esmoquin o frac.

    .row.mb-5
      .col-auto
        .mb-0.px-5.py-2.caja-titulo-bordes-especial.w-auto.fw-bold Perillas

    p.mb-5 Las perillas de la camisa por lo regular son de dos tipos, uno en el frente de esta reforzada con entretela para que all se alojen los ojales y botones y otra en las mangas con puo, tambin llamada la portauela o vista de la manga, como se vio en el trazo bsico de la camisa; esta es una pieza que tapa el corte de la manga aportndole una funcionalidad adicional. En la camiseta tipo polo la perilla frontal es ms corta ya que cumple la funcin de ajustar el cuello al cerrarse.
      br
      br
      | Se debe determinar si la perilla frontal va incluida en el delantero o ser independiente pues esto influir en la forma de trazarla y las mrgenes de costuras.

    .row.justify-content-center.mb-5
      .col-lg-10.mb-5
        p.mb-5.text-center.py-2.px-5.caja.temporal--azul A continuacin, algunas de las perillas ms usuales y como se presenta el doblez y la costura para su montaje:
        .row.justify-content-center
          .col-lg-6.mb-5
            div
              figure.mb-3
                img(src="@/assets/curso/tema2-imagen99.svg")
                figcaption Fuente: folguiasorion.com/services/camisa/ (s.f.)
              .row.align-items-center
                .col-lg-auto
                  .circle
                .col-lg
                  p.mb-0 Perilla con doblez superior con o sin alma: Viene incluida en el delantero y su doblez voltea hacia el revs de la tela.
          .col-lg-6.mb-5
            div
              figure.mb-3
                img(src="@/assets/curso/tema2-imagen100.svg")
                figcaption Fuente: folguiasorion.com/services/camisa/ (s.f.)
              .row.align-items-center
                .col-lg-auto
                  .circle
                .col-lg
                  p.mb-0 Perilla con tipo pellizco derecha con o sin alma: se le adiciona aparte del ancho de la perilla y de las costuras el pellizco que por lo regular es de  de pulgada.
          .col-lg-6.mb-lg-0.mb-5
            div
              figure.mb-3
                img(src="@/assets/curso/tema2-imagen101.svg")
                figcaption Fuente: folguiasorion.com/services/camisa/ (s.f.)
              .row.align-items-center
                .col-lg-auto
                  .circle
                .col-lg
                  p.mb-0 Perilla sobre puesta con entretela individual en dos agujas: la perilla es independiente y va encima del delantero y se le debe agregar la costura a este.
          .col-lg-6
            div
              figure.mb-3
                img(src="@/assets/curso/tema2-imagen102.svg")
                figcaption Fuente: folguiasorion.com/services/camisa/ (s.f.)
              .row.align-items-center
                .col-lg-auto
                  .circle
                .col-lg
                  p.mb-0 Perilla tipo pretina recta: Esta perilla va totalmente independiente y se aade al delantero no va encima o esta aadida a ella como las anteriores y su forma de unin como de terminado es igual a una pretina de pantaln.

      .col-lg-6
        figure
          img(src="@/assets/curso/tema2-imagen103.svg")

    .row.mb-5
      .col-auto
        .mb-0.px-5.py-2.caja-titulo-bordes-especial.w-auto.fw-bold Tapas o Tapetas

    p.mb-5 Son un complemento a los bolsillos y cumplen la funcin tanto de cierre como aportante al diseo de la camisa o pantaln. En su trazo se toma en cuenta dos factores, el primero es el ancho del bolsillo donde se aplicarn y el segundo el diseo inferior de este.

    .row.justify-content-center.mb-5
      .col-lg-10
        figure
          img(src="@/assets/curso/tema2-imagen104.svg")

    .row.mb-5
      .col-auto
        .mb-0.px-5.py-2.caja-titulo-bordes-especial.w-auto.fw-bold Charreteras

    .row.justify-content-center
      .col-lg-6.mb-lg-0.mb-5
        p.mb-0 Tambin llamada jarretera o jareta. Su utilidad es sostener o dar soporte en el hombro, sin embargo, en nuestro tiempo da cierto manejo de diseo, entre los cuales est el soporte de manga, adonar bolsillos, en la espalda en la amilla y por supuesto en los hombros.
          br
          br
          | En cuanto a su trazo esta vara entre 2.5 cm y 3.5 cm de ancho y el largo corresponde al lugar donde ser aplicada. En  algunas de ellas su terminacin es en flecha y / o rectangular con ojal para sujecin con botn.

      .col-lg-6
        figure
          img(src="@/assets/curso/tema2-imagen105.svg")
    .row.justify-content-center
      .col-lg-6
        p.mb-0 Camisa, s.f.; HISTORIA DE LA MODA - FASHION HISTORY : CUELLOS, s.f.; Tipos de Puo de Camisa, 2014; Hatem, 2017; Nuez de Arenas, 2016; Wikipedia, s.f.

