    #t_2_6.titulo-segundo
      h2 2.6    Escalado de la camisa

    p.mb-5
      span.texto-rosa GRADACION O ESCALADO DE LA CAMISA
      br
      br
      | El escalado o gradacin es la tcnica por utilizar para la generacin de las tallas mayores o inferiores al patrn base, sin necesidad de desarrollar cada patrn desde cero.

    .row.justify-content-center.caja.temporal--pink.g-0
      .col-lg-3.mb-lg-0.mb-5
        figure
          img(src="@/assets/curso/tema2-imagen106.jpg")
      .col-lg-9
        p.mb-0.px-5.py-4 Se le llama talla base a la talla que usamos para elaborar el patrn inicial o central del cual se partir la gradacin tanto en sentido positivo (tallas superiores) como en negativo (tallas inferiores).
          br
          br
          | Para lograr que el escaldo sea equitativo, el cuadro de tallas es fundamental pues brinda la gua en los incrementos entre talla y talla tanto en direccin horizontal como  vertical.
          br
          br
          | Se parte de los ejes verticales de centro frente, hombro y cuello como puntos fijos a partir de los cuales se empieza a elaborar el plano cartesiano para desarrollar la particin de tallas.

    .pt-5.contenedor-slider-tema2
