    p.mb-5 Como se dijo al inicio, partiendo del patrn bsico de camisa y sus complementos, se pueden conseguir diferentes desarrollos de modelos a partir de la variacin en algunas piezas o detalles, modificar mangas, cuellos, ajuste, cortes, todas estas transformaciones darn como resultado diferentes propuestas de diseos.

