      .col-lg-8
        p.mb-0 La vista es la pieza que va sobrepuesta en el fondo de bolsillo N 1 y se corta en la misma tela del pantaln, esta pieza no permite que el fondo de bolsillo se vea que va en diferente tela.
          br
          br
          | Marcar en otro papel el fondo de bolsillo N1, trazar la diagonal de la boca de bolsillo y luego una paralela de 5 cm hacia el interior del bolsillo (La vista es la zona que est sombreada).

