      .col-lg-10
        p.mb-5 La aletilla es una aleta que se le coloca al lado izquierdo de la parte superior del tiro delantero y sirve para ubicar el cierre del pantaln en el momento de la confeccin. Para la elaboracin de este molde se han estandarizado ciertas medidas que corresponden a la distancia de la recta del tiro desde la cintura hasta donde empieza la curva de tiro o gaviln.
          br
          br
          | Dichas medidas son:
        .row.justify-content-center.mb-5
          .col-lg-4.mb-lg-0.mb-5
