              li
                .lista-ol--cuadro__vineta.circle.color-acento-contenido
                  span 2
                | Para las tallas 34, 36 y 38 de 21 cm de largo por 5 cm de ancho.
              li
                .lista-ol--cuadro__vineta.circle.color-acento-contenido
                  span 3
                | Para las tallas 40 y 42 de 24 cm de largo por 5 cm de ancho.

