          .col-lg-5
            p.mb-0.caja-titulo-bordes-especial.normal.text-center.py-2 Aletilln

        p.mb-5 El aletilln es una aleta doble que se le coloca al lado derecho de la parte superior del tiro delantero y sirve para proteger la piel de cualquier accidente en el momento que se est subiendo el cierre.
          br
          br
          |  As como en el caso de la aletilla, ya se cuentan con unas medidas estandarizadas segn la talla de la siguiente manera:

        .row.justify-content-center
          .col-lg-8
            .tabla-a.color-acento-botones.mb-5
              table
                thead
                  tr
                    th Talla
                    th Largo
                    th Ancho
                tbody
                  tr
                    td 28  30  32
                    td 19 cm
                    td 10 cm
                  tr
                    td 34  36  38
                    td 21 cm
                    td 10 cm
                  tr
                    td 40  42
                    td 24 cm
                    td 10 cm
            p.mb-0 Primero realizar un rectngulo de 19 cm de largo por 10 cm de ancho, ste corresponde al estndar de una talla 32. Luego ubicar un piquete en la mitad de cada una de las lneas horizontales.
          .col-lg-4
            figure
              img(src="@/assets/curso/tema2-imagen163.svg")
              figcaption Fuente: elaboracin propia en Optitex.

        .row.justify-content-center.mb-4
          .col-lg-5
            p.mb-0.caja-titulo-bordes-especial.normal.text-center.py-2 Pretina

        p.mb-5 La pretina es la cinta que se utiliza para sujetar el pantaln de la cintura y se ubica en la parte superior de ste. Para la elaboracin de este molde bsico es necesario la sumatoria de las medidas de: contorno de cintura ms ancho de aletilla ms mitad () del ancho de aletilln.

        .cajon.color-primario.p-5.mb-4
          p.mb-0.text-center Para construirla se realiza un rectngulo con el resultado de esta sumatoria por el ancho de pretina deseado, este puede variar de acuerdo al diseo. El ancho de una pretina bsica es de 9 cm, la cual al ser confeccionada quedara de 3.5 cm (este ancho de pretina vara de acuerdo al diseo), ya que se usan 2 cm para costura entalegada y se dobla a la mitad.

        p.mb-5 Luego, ubicar sobre las lneas horizontales del rectngulo la mitad y a cada lado 5 cm con un piquete sobre cada una de ellas, estos piquetes son de ayuda en el momento de la confeccin.

