      .col-lg-7
        figure
          img(src="@/assets/curso/tema2-imagen164.svg").mb-3
          figcaption Fuente: elaboracin propia en Optitex.

    .row.justify-content-center.mb-5
      .col-lg-10
        .row.justify-content-center.mb-4
          .col-lg-5
            p.mb-0.caja-titulo-bordes-especial.normal.text-center.py-2 Ribete

        p.mb-5 El ribete es una cinta o tira de tela con que se adorna y refuerza la orilla de la boca del bolsillo.
          br
          br
          | Este molde es solo un simple rectngulo que equivale a la medida de la boca del bolsillo + 5 cm para terminaciones de confeccin por un ancho de 8 cm

        .row.justify-content-center.mb-5
          .col-lg-7
            figure
              img(src="@/assets/curso/tema2-imagen165.svg").mb-3
              figcaption Fuente: elaboracin propia en Optitex.

        .row.justify-content-center.mb-0
          .col-lg-5
            p.mb-0.caja-titulo-bordes-especial.normal.text-center.py-2 Fondo de bolsillo ribete

        .px-5.py-4.caja.temporal--rosa.mb-5
          p.mb-0 Es la pieza en la que se guardan las pertenencias en el bolsillo posterior, va en la tela empleada para los forros de bolsillos o fondos de bolsillos delanteros.
            br
            br
            | Para realizar el molde de esta pieza se traza un rectngulo del ancho del bolsillo ms 5 cm, por el largo del bolsillo (16 cm) por 2 ms 8 cm ms la distancia del bolsillo a la cintura (10 cm o 12 cm), as 16+16+8+12=52 cm

        .row.justify-content-center.mb-4
          .col-lg-5
            p.mb-0.caja-titulo-bordes-especial.normal.text-center.py-2 Fondo de bolsillo

        .row.justify-content-center.mb-5
          .col-lg-7
            p.mb-4 El fondo de bolsillo es una bolsa de tela; en el caso del bolsillo ribete se usa generalmente para guardar la billetera en el bolsillo del lado izquierdo y en pauelo en el lado derecho, este molde tambin se desarrolla con una medida estndar para todas las tallas.
            figure
              img(src="@/assets/curso/tema2-imagen165.svg").mb-3
              figcaption Fuente: elaboracin propia en Optitex.

        .caja.temporal--azul.mb-5.py-5
          .row.justify-content-center.mb-4
            .col-lg-5
              p.mb-0.caja-titulo-bordes-especial.normal.text-center.py-2 Vista del fondo de bolsillo
          .row.justify-content-center.mb-5
            .col-lg-7
              p.mb-0 Al igual que la vista del bolsillo lateral es la pieza que va sobrepuesta en la parte interna del fondo de bolsillo y va en la misma tela del resto del pantaln, esta pieza tambin se traza con medidas estndar.
          .row.justify-content-center.mb-4
            .col-lg-5
              p.mb-0.caja-titulo-bordes-especial.normal.text-center.py-2 Pasador
          .row.justify-content-center.mb-0
            .col-lg-7
              p.mb-4  Los pasadores son tiras fijadas en sus extremos que se colocan en el contorno de la pretina y sirven para sostener la correa. El tamao, la forma y la cantidad de pasadores depende del diseo que se est realizando, sin embargo, el <i>jean</i> clsico lleva cinco pasadores de 1.2 cm de ancho por 6.5 cm o 7 cm de largo. Se ubican as: uno en cada pernera delantera justo en la boca de bolsillo, uno en el centro del tiro posterior y uno en cada pernera posterior a 4 cm o 5 cm del costado.  Para construirlo se realiza un rectngulo de 3.5 cm de ancho por 40 cm de largo.
