    .row.justify-content-center.mb-5
      .col-lg-4
        p.mb-4.text-center.py-2.caja-titulo-bordes-especial.normal Bolsillo delantero
    .row.justify-content-center.mb-5
      .col-lg-6
        p.mb-0.text-center Para la construccin del bolsillo y sus componentes se trabaja con la pernera delantera.
    .row.mb-4
      .col-lg-6.mb-lg-0.mb-5.px-5.py-4.caja.temporal--azul
        p.mb-4.text-center
          strong Boca de bolsillo
        p.mb-4 Desde el vrtice de cintura y sobre el costado hacia abajo medir 5 cm y sobre la lnea de cintura 11.5 cm, trazar en curva la boca de bolsillo.
        figure.px-5
          img(src="@/assets/curso/tema2-imagen206.svg")
          figcaption Fuente: elaboracin propia en Optitex.
      .col-lg-6.px-5.py-4.caja.temporal--rosa
        p.mb-4.text-center
          strong Falso
        p.mb-4 Para su desarrollo se marca el nuevo delantero y se dan 6cm paralelos a la boca de bolsillo, desde la lnea de cintura hasta la lnea de costado.
        figure.px-5
          img(src="@/assets/curso/tema2-imagen207.svg")
          figcaption Fuente: elaboracin propia en Optitex.
    .row.justify-content-center.mb-5
      .col-lg-4
        p.mb-4.text-center.py-2.caja-titulo-bordes-especial.normal Fondo de bolsillo / Fondo No 1
    SlyderEspecial.mb-5(type="especial" :inverted="true" :datos="datosSlyder14")
    .row.justify-content-center.mb-5
      .col-lg-10
        .row.justify-content-center.align-items-center.background1-especial2.border-radius.left.right.mb-5
          .col-auto.background1-especial1.border-radius.right
            figure.p-4
              img(src="@/assets/curso/tema1-imagen63.svg")
          .col
            p.mb-0 Nota: el fondo de bolsillo 1 es el que esta sombreado, se le marca la lnea de aplome y se corta 2 por talla.
    .row.justify-content-center.mb-5
      .col-lg-4
        p.mb-0.text-center.py-2.caja-titulo-bordes-especial.normal Fondo No 2
    .row.justify-content-center.mb-5
      .col-lg-6.mb-lg-0.mb-5.d-flex
        p.mb-4.p-5.caja-gris1 Se traza en otro papel el fondo de bolsillo N1, seguidamente se sobrepone el delantero con la variacin en el costado para marcar la boca de bolsillo. Se marca y elimina el sobrante que corresponde a la boca de bolsillo.
      .col-lg-6.d-flex
        p.mb-4.p-5.caja-gris1 El fondo de bolsillo N 2 es el que esta sombreado, se le marca la lnea de aplome y se corta 2 por talla.
    .row.justify-content-center.mb-5
      .col-lg-6.mb-lg-0.mb-5
        figure
          img(src="@/assets/curso/tema2-imagen212.svg")
          figcaption Fuente: elaboracin propia en Optitex.
      .col-lg-6
        figure
          img(src="@/assets/curso/tema2-imagen213.svg")
          figcaption Fuente: elaboracin propia en Optitex.
    .row.justify-content-center.mb-5
      .col-lg-4
        p.mb-0.text-center.py-2.caja-titulo-bordes-especial.normal Vista
    .row.justify-content-center.mb-5
      .col-lg-8
        p.mb-4 La vista es la pieza que va sobrepuesta en el fondo de bolsillo N 1 y se corta en la misma tela del pantaln, sta pieza no permite que el fondo de bolsillo se vea que va en diferente tela.
          br
          br
          | Marcar en otro papel el fondo de bolsillo N1 y marcarle la boca del bolsillo y el paralelo de 6 cm del falso.
        figure
          img(src="@/assets/curso/tema2-imagen214.svg")
          figcaption Fuente: elaboracin propia en Optitex.
    .row.justify-content-center.mb-5
      .col-lg-4
        p.mb-0.text-center.py-2.caja-titulo-bordes-especial.normal Relojera
    .row.justify-content-center.mb-5
      .col-lg-8
        p.mb-4 La relojera es un pequeo bolsillo que va en la vista del bolsillo derecho, se usaba para guardar el reloj de bolsillo, de all su nombre.
        .cajon.color-primario.p-5.mb-4
          p.mb-0.text-center Para la relojera, marcar la vista en un papel nuevo y del costado 2 cm hacia adentro, trazar una lnea paralela a la lnea de aplome. Del punto de cintura que une con la boca (piquete) entrar 2 cm y marcar una lnea paralela al aplome. De la lnea de cintura bajar 2 cm y trazar una lnea perpendicular (90) a la lnea de aplome, formando la relojera.
      .col-lg-7
        figure
          img(src="@/assets/curso/tema2-imagen215.svg")
          figcaption Fuente: elaboracin propia en Optitex.
    .row.justify-content-center.mb-5
      .col-lg-4
        p.mb-0.text-center.py-2.caja-titulo-bordes-especial.normal Aletilla
    .row.justify-content-center
      .col-lg-8
        p.mb-4 La aletilla es una aleta que se le coloca al lado izquierdo de la parte superior del tiro delantero y sirve para ubicar el cierre del pantaln en el momento de la confeccin. Para la elaboracin de este molde se han estandarizado ciertas medidas que corresponden a la distancia de la recta del tiro desde la cintura hasta donde empieza la curva de tiro o gaviln.
        p.mb-4.text-center
          strong Dichas medidas son:
      .col-lg-6
        ol.lista-ol--cuadro.lista-ol--separador.list-with--borders.py-4.mb-5
          li
            .lista-ol--cuadro__vineta.circle.color-acento-contenido
              span 1
            | Para las tallas 28, 30 y 32 de 19 cm de largo por 5 de ancho.
          li
            .lista-ol--cuadro__vineta.circle.color-acento-contenido
              span 2
            | Para las tallas34, 36 y 38 de 21 cm de largo por 5 cm de ancho.
          li
            .lista-ol--cuadro__vineta.circle.color-acento-contenido
              span 3
            | Para las tallas 40 y 42 de 24 cm de largo por 5 cm de ancho.
        .cajon.color-acento-contenido.p-5.mb-4
          p.mb-0.text-center Para su construccin de parte de un rectngulo de 19 cm de largo por 5 cm de ancho, ste corresponde al estndar de una talla 32.
        figure.mb-5
          img(src="@/assets/curso/tema2-imagen216.jpg")
          figcaption Fuente: elaboracin propia en Optitex.
        .cajon.color-primario.p-5.mb-4
          p.mb-0.text-center Suavizar el extremo inferior izquierdo de la aletilla con curva.
        figure.mb-5
          img(src="@/assets/curso/tema2-imagen217.jpg")
          figcaption Fuente: elaboracin propia en Optitex.
    .row.justify-content-center.mb-5
      .col-lg-4
        p.mb-0.text-center.py-2.caja-titulo-bordes-especial.normal Aletilln
    .row.justify-content-center.mb-4
      .col-lg-8
        p.mb-4 El aletilln es una aleta doble que se le coloca al lado derecho de la parte superior del tiro delantero y sirve para proteger la piel de cualquier accidente en el momento que se est subiendo el cierre.
          br
          br
          | As como en el caso de la aletilla, ya se cuentan con unas medidas estandarizadas segn la talla de la siguiente manera:
        .tabla-a.color-acento-contenido.mb-5
          table
            thead
              tr
                th Talla
                th Largo
                th Ancho
            tbody
              tr
                td 28  30  32
                td 19 cm
                td 10 cm
              tr
                td 34  36  38
                td 21 cm
                td 10 cm
              tr
                td 40  42
                td 24 cm
                td 10 cm
        p.mb-4 Primero realizar un rectngulo de 19 cm de largo por 10 cm de ancho, ste corresponde al estndar de una talla 32. Luego ubicar un piquete en la mitad de cada una de las lneas horizontales.
      .col-lg-6
        figure.mb-5
          img(src="@/assets/curso/tema2-imagen218.jpg")
          figcaption Fuente: elaboracin propia en Optitex.
      .col-lg-10
        .row.align-items-center(style="background: #B3E2FA")
          .col-lg-4
            figure.mb-0
              img(src="@/assets/curso/tema2-imagen219.jpg")
          .col-lg-8
            p.mb-0 Para la elaboracin de los <i>jeans</i> se pueden trazar pretinas rectas o pretinas anatmicas, teniendo ests ltimas un mejor ajuste sobre el cuerpo:
    .row.justify-content-center.mb-5
      .col-lg-4
        p.mb-0.text-center.py-2.caja-titulo-bordes-especial.normal Pretina recta
    .row.justify-content-center.mb-5
      .col-lg-10.mb-4
        p.mb-4 La pretina es la cinta que se utiliza para sujetar el pantaln de la cintura y se ubica en la parte superior de ste. Para la elaboracin de este molde bsico es necesario la sumatoria de las medidas de: contorno de cintura ms ancho de aletilla ms mitad () del ancho de aletilln.
          br
          br
          | Para construirla se realiza un rectngulo con el resultado de esta sumatoria por el ancho de pretina deseado, este puede variar de acuerdo al diseo. El ancho de una pretina bsica es de 10 cm, la cual al ser confeccionada quedara de 4 cm, ya que se usan 2 cm para costura entalegada y se dobla a la mitad.
          br
          br
          | Luego, ubicar sobre las lneas horizontales del rectngulo la mitad y a cada lado 5 cm con un piquete sobre cada una de ellas, estos piquetes son de ayuda en el momento de la confeccin.
      .col-lg-9
        figure.mb-0
          img(src="@/assets/curso/tema2-imagen220.jpg")
          figcaption Fuente: elaboracin propia en Optitex.
    .row.justify-content-center.mb-5
      .col-lg-4
        p.mb-0.text-center.py-2.caja-titulo-bordes-especial.normal Pretina anatmica o curva
    .row.justify-content-center.mb-5
      .col-lg-10.mb-4
        p.mb-0 La pretina anatmica se logra uniendo por el costado las pretinas resultantes del trazo (delantera y posterior), se marca la lnea de aplome en donde estaba el costado. Se le aumenta el ancho de la aletilla en la cabeza o centro frente.
      .col-lg-8
        figure.mb-0
          img(src="@/assets/curso/tema2-imagen221.svg")
          figcaption Fuente: elaboracin propia en Optitex.
    .row.justify-content-center.mb-5
      .col-lg-4
        p.mb-0.text-center.py-2.caja-titulo-bordes-especial.normal Pasador
    .row.justify-content-center.mb-5
      .col-lg-10.mb-4
        p.mb-0 Los pasadores son tiras fijadas en sus extremos que se colocan en el contorno de la pretina y sirven para sostener el cinturn.
          br
          br
          | El tamao, la forma y la cantidad de pasadores depende del diseo que se est realizando, sin embargo, el <i>jean</i> clsico lleva cinco pasadores de 1.2 cm de ancho por 6.5 cm o 7 cm de largo. Se ubican as: uno en cada pernera delantera justo en la boca de bolsillo, uno en el centro del tiro posterior y uno en cada pernera posterior a 4 cm o 5 cm del costado.
          br
          br
          | Para construirlo se realiza un rectngulo de 3.5 cm de ancho por 40 cm de largo.
      .col-lg-8
        figure
          img(src="@/assets/curso/tema2-imagen222.svg")
          figcaption Fuente: elaboracin propia en Optitex.
    .row.justify-content-center.mb-5
      .col-lg-4
        p.mb-0.text-center.py-2.caja-titulo-bordes-especial.normal Mrgenes de costuras
    .row.justify-content-center
      .col-lg-10
        p.mb-5 Como este patrn sale sin medidas de costuras se le deben adicionar, es importante recordar que cuando se hace un corte, como por ejemplo en la boca del bolsillo o en la cotilla, se deben adicionar las respectivas mrgenes de costura, as el patrn salga con las ventajas de costuras.
          br
          br
          | Para la pernera delantera se adicionan costuras as:
        .row.justify-content-center.mb-5
          .col-lg-8.mb-4
            .cajon.color-primario.p-5.mb-4
              p.mb-0.text-center 1cm en boca de bolsillo, cintura y tiro para encarterar en plana a 1 cm.
            p.mb-4 Dependiendo de la mquina con que se hagan las operaciones de entrepierna y costado ser la margen de costura, si se cerrarn en fileteadora puntada de seguridad se deja 1 cm de costura y para cerradora de codo se le da entre 1.5 cm o 1.7 cm (incluso hasta 2 cm) dependiendo de la gua a emplear y el peso de la tela. Para el ejemplo se har en cerradora de codo la entrepierna y en fileteadora puntada de seguridad el costado.
            figure
              img(src="@/assets/curso/tema2-imagen224.jpg")
          .col-lg-4.mb-4
            figure
              img(src="@/assets/curso/tema2-imagen225.svg")
              figcaption Fuente: elaboracin propia en Optitex.
          .col-lg-10
            .cajon.color-acento-contenido.p-5.mb-5
              p.mb-0.text-center Para el bajo se da 1.5 cm de ruedo (o segn el diseo) y 1 cm de dobladillo, se debe dobladillar el papel como quedar la costura para luego cortar por la entrepierna y costado, a esto se le llama descargue de costura.
          .col-lg-8
            figure
              img(src="@/assets/curso/tema2-imagen226.svg")
              figcaption Fuente: elaboracin propia en Optitex.
        .row.justify-content-center.mb-4
          .col-lg-6.d-flex
            .caja.temporal--azul.p-4
              p.mb-4 Continuando con los complementos de la pernera delantera, se da costuras al falso. Como este sale de la boca de bolsillo y es all donde va pegado, lleva las mismas mrgenes que la boca: 1 cm en costado, boca y cintura. En la lnea paralela a la boca, va filete sencillo (solo para evitar que se deshilache) y pegada sobre el fondo 2 en plana, por lo que no lleva ventaja de costuras.
              figure
                img(src="@/assets/curso/tema2-imagen227.svg")
                figcaption Fuente: elaboracin propia en Optitex.
          .col-lg-6.d-flex
            .caja.temporal--rosa.p-4
              p.mb-4 La vista lleva las mismas mrgenes que la pernera en costado y cintura y en la curvatura va filete sencillo (solo para evitar que se deshilache) y pegada sobre el fondo 1 en plana, por lo que no lleva ventaja de costuras. Para facilitar la confeccin se hace un piquete en cada uno de los puntos que coincidirn con la boca de bolsillo.
              figure
                img(src="@/assets/curso/tema2-imagen228.svg")
                figcaption Fuente: elaboracin propia en Optitex.
        p.mb-4 La relojera es un bolsillo de parche que se confecciona as: los laterales van doblados (planchados) y pegados en 2 agujas por lo que se les da 1 cm de margen (tener en cuenta el descargue en la curva inferior), en el fondo va fileteado junto con la vista por lo que no lleva margen de costura, en la boca va doblado a 1.5 cm y dobladillado a 1 cm (se cose en dobladillo igual que el ruedo).
        .row.justify-content-center.mb-4
          .col-lg-10.mb-4
            figure.mb-4
              img(src="@/assets/curso/tema2-imagen229.svg")
              figcaption Fuente: elaboracin propia en Optitex.
            p.mb-4 Los fondos de bolillo van encarterados y unidos a las piezas de la pernera, por lo que llevan la misma ventaja de costura que stas, 1 cm, en este caso por todo el contorno.
          .col-lg-8
            figure
              img(src="@/assets/curso/tema2-imagen230.svg")
              figcaption Fuente: elaboracin propia en Optitex.
    .row.justify-content-center.mb-5
      .col-lg-4
        p.mb-0.text-center.py-2.caja-titulo-bordes-especial.normal Pernera posterior
    .row.justify-content-center
      .col-lg-10
        p.mb-4 Como la pernera posterior fue partida por la parte de la cotilla se le deben dar costuras a ambas piezas (pernera y cotilla) segn la mquina y la operacin en la que se trabajar, para el caso del ejemplo se trabajar con fileteadora puntada se seguridad (1 cm) y se asentar o pespuntar en 2 agujas hacia la pernera, este dato es importante para saber que se debe hacer el descargue hacia abajo, es decir hacia la pernera. En la entrepierna y el costado se maneja la misma margen de costura que en la pernera delantera: 1 cm en costado y 1.5 cm o 1,7 cm en entrepierna (esto segn el diseo).
      .col-lg-8
        .row.justify-content-center
          .col-lg-6.d-flex
            .caja.temporal--rosa.p-4
              p.mb-0 Cmo lleva el bolsillo posterior de parche, se deben hacer las marcas de donde va, para hacer la plantilla (perforaciones) y poder marcar la tela.
          .col-lg-6.d-flex
            .caja.temporal--rosa.p-4
              p.mb-0 Para el ruedo, se procede igual que en la pernera delantera.
        .row.justify-content-center
          .col-lg-6.mb-5
            figure
              img(src="@/assets/curso/tema2-imagen231.svg")
              figcaption Fuente: elaboracin propia en Optitex.
          .col-lg-6.mb-5
            figure
              img(src="@/assets/curso/tema2-imagen232.svg")
              figcaption Fuente: elaboracin propia en Optitex.
          .col-lg-12
            .cajon.color-acento-contenido.p-5.mb-4
              p.mb-0.text-center La cotilla lleva las mismas mrgenes de costura que la pernera 1cm por el contorno.
    .row.justify-content-center
      .col-lg-6
        figure.mb-5
          img(src="@/assets/curso/tema2-imagen233.svg")
          figcaption Fuente: elaboracin propia en Optitex.
        p.mb-5 El bolsillo se trabaja muy similar a la relojera, en los laterales y base, va doblado 1 cm (planchado) y pegado en 2 agujas; en la boca va doblado a 1,5 cm y dobladillado a 1 cm (o segn el diseo).
        figure.mb-5
          img(src="@/assets/curso/tema2-imagen234.svg")
          figcaption Fuente: elaboracin propia en Optitex.
        p.mb-5 En el caso de emplear la pretina anatmica. Esta ya cuenta con el avance para la aletilla y el aletilln (agregado en el momento del trazado), por lo tanto, se deber dar mrgenes de costura de 1cm (van encarteradas) en cintura inferior (la que une con el pantaln), tiro posterior (une con ella misma) y la cintura superior (la que une con ella misma y da terminacin a la prenda).
        figure.mb-5
          img(src="@/assets/curso/tema2-imagen235.svg")
          figcaption Fuente: elaboracin propia en Optitex.
        p.mb-5 En el caso de la pretina recta, sta se deber verificar que tenga el largo y ancho adecuados, pues se realiza con las costuras incluidas.
          br
          br
          | Los pasadores, aletilla y aletilln, no llevan ventajas de costuras adicionales, pues ya se tuvieron en cuenta al momento del trazo.
    .row.justify-content-center.mb-5
      .col-lg-4
        p.mb-4.text-center.py-2.caja-titulo-bordes-especial.normal Sealizacin e industrializacin
        p.mb-0 Los patrones se deben marcar con la siguiente informacin:
    .row.justify-content-center.mb-5
      .col-lg-6
        .row.justify-content-center.mb-4
          .col-lg-6
            ol.lista-ol--cuadro
              li
                .lista-ol--cuadro__vineta
                  span 1
                | Nombre o referencia del modelo
              li
                .lista-ol--cuadro__vineta
                  span 2
                | Nombre de la pieza
              li
                .lista-ol--cuadro__vineta
                  span 3
                | Hilo o aplome
              li
                .lista-ol--cuadro__vineta
                  span 4
                | Material
              li
                .lista-ol--cuadro__vineta
                  span 5
                | Cantidad por talla
          .col-lg-6
            figure.mb-5
              img(src="@/assets/curso/tema2-imagen236.svg")
        p.mb-5 Y en el caso de requerir marcas internas se hacen los piquetes y seas.
          br
          br
          | Pernera delantera y pernera posterior: lleva dos de cada una, (una para cada pierna) y se cortan en la tela principal.
        figure.mb-5
          img(src="@/assets/curso/tema2-imagen237.svg")
          figcaption Fuente: elaboracin propia en Optitex.
      .col-lg-7
        p.mb-4.text-center Al igual que las perneras, la cotilla, los bolsillos posteriores, los falsos y las vistas se cortan en la tela principal y dos por talla.
        figure.mb-4
          img(src="@/assets/curso/tema2-imagen238.svg")
          figcaption Fuente: elaboracin propia en Optitex.
        p.mb-5.text-center La pretina anatmica se corta cuatro veces en la tela principal.
      .col-lg-6
        figure.mb-4
          img(src="@/assets/curso/tema2-imagen239.svg")
          figcaption Fuente: elaboracin propia en Optitex.
        p.mb-5.text-center La relojera se corta una sola vez en la tela principal.
    .row.justify-content-center.mb-5
      .col-lg-5
        figure.mb-4
          img(src="@/assets/curso/tema2-imagen240.svg")
          figcaption Fuente: elaboracin propia en Optitex.
    p.mb-5.text-center Los fondos de bolsillo se cortan dos veces cada uno en tela de forro.
    .row.justify-content-center.mb-5
      .col-lg-9
        figure
          img(src="@/assets/curso/tema2-imagen241.svg")
          figcaption Fuente: elaboracin propia en Optitex.
    p.mb-5.text-center En el caso de hacer la pretina recta esta se corta una sola vez en la tela principal.
    .row.justify-content-center.mb-5
      .col-lg-9
        figure
          img(src="@/assets/curso/tema2-imagen242.svg")
          figcaption Fuente: elaboracin propia en Optitex.
    .row.justify-content-center.mb-5
      .col-lg-4
        p.mb-0.text-center.py-2.caja-titulo-bordes-especial.normal.azul Adaptado de Gutirrez et al., 2011; SENA, s.f.-c.

    #t_2_10.titulo-segundo
      h2 2.10   Tipos de bolsillos

    figure.mb-4
      img(src="@/assets/curso/tema2-imagen243.svg")
    p.mb-5 Los bolsillos aparte de su utilidad y como complemento de las prendas, son uno de los detalles ms utilizados, hablando desde el diseo. Existen tantas variaciones de los bolsillos como diseos mismos en el mundo de la moda. En esta seccin se trabajarn los ms conocidos y sus variaciones.

    .row.justify-content-center.mb-5
      .col-lg-4
        p.mb-0.text-center.py-2.caja-titulo-bordes-especial.normal.rosa Se pueden clasificar en las siguientes categoras:

    .row.justify-content-center.mb-5
      .col-lg-8
        figure
          img(src="@/assets/curso/tema2-imagen244.svg")
          figcaption Fuente: elaboracin propia en Corel Draw.

    .row.justify-content-center.mb-5
      .col-lg-10
        p.mb-4 Como su nombre lo indica son los bolsillos que se pegan sobre la pieza como un parche. Estn formados desde una sola pieza geomtrica aplicados en la prenda uniendo sus laterales y su parte inferior. Estos bolsillos tambin se llaman sobrepuestos y de plastrn. Se emplean en todo tipo de prendas (camisas, chaquetas, pantalones, bermudas, sacos, entre otras). Los hay cuadrados, rectangulares, redondeados, sin lmite de formas. Algunos de ellos llevan tapa que se ajusta con un botn.
        figure.mb-5
          img(src="@/assets/curso/tema2-imagen245.svg")
        .cajon.color-primario.p-4.mb-4
          p.mb-0.text-center Para desarrollar los diseos de los bolsillos es importante tener en cuenta las dimensiones de ancho, largo y en el caso de llevar formas esas medidas particulares. Este tipo de bolsillo va doblado por su contorno (excepto en la boca) y pegado a la prenda generalmente con puntada sencilla (plana) o con 2 agujas, por lo que usualmente se les deja 1 cm de mrgenes de costura.
        p.mb-5 En la boca de bolsillo habr que revisar cmo se propone en el diseo, las hay fileteadas y dobladas (solo la margen de doblado), hay dobladilladas (margen de doblado y de dobladillo), sesgadas o ribeteadas (no llevan margen), por lo tanto, segn el diseo se deben asignar las mrgenes de costura. Se elabora una plantilla de planchado de 2 cm menos de ancho x 2 cm menos de largo, con ella se doblarn las costuras para facilitar la confeccin del bolsillo.

    .row.justify-content-center.mb-5
      .col-lg-8
        figure.mb-5
          img(src="@/assets/curso/tema2-imagen246.svg")
        .row.justify-content-center.caja.temporal--rosa.p-4.rounded-3.mb-4
          .col-lg-4
            figure
              img(src="@/assets/curso/tema2-imagen247.svg")
          .col-lg-8
            .h4.texto-rosa Bolsillo integrado
            p.mb-0 Es aquel bolsillo que se encuentra superficialmente (internamente) en la prenda y requieren que se realice una abertura a la pieza exterior que no coincide con las costuras de construccin. se pega junto con el forro y se acompaa en su mayora con una tapa o solapa.
        .row.justify-content-center.p-4.rounded-3.mb-4
          .col-lg-4
            figure
              img(src="@/assets/curso/tema2-imagen248.svg")
          .col-lg-8
            .h4.texto-rosa Bolsillo de ribete u ojal
            p.mb-0 Los bolsillos de ribete, como su nombre lo indica, son aquellos que se caracterizan por ser internos y se accede a ellos por medio de una abertura en la prenda que, generalmente, est ribeteada, por ello su nombre. Los hay sin pestaa (solo la caja o abertura), con una pestaa o dos pestaas, algunos se confeccionan con tapa la cual puede ser externa, o estar pegada al interior del bolsillo y salir por medio de la abertura o ribete, tambin los hay con cremallera.
        .caja.temporal--azul.p-5
          p.mb-2 Es un tipo de bolsillo totalmente clsico, se usa tanto en prendas masculinas como femeninas, tiene un vnculo con prendas de sastrera y de vestuario formal como de etiqueta y de uniformes, son muy comunes en los pantalones, chalecos, sacos (blazer), chaquetas, sudaderas y pantalonetas, entre otros.
          .row.justify-content-center
            .col-lg-6
              figure
                img(src="@/assets/curso/tema2-imagen249.svg")
        p.mb-5 Es un bolsillo interior que tiene uno o dos ribetes de tela que se unen en el centro, se debe fusionar entretela donde se va a ubicar dependiendo de la calidad y grosor del material, se realiza en sentido vertical, horizontal o diagonal, dependiendo del tipo de prenda superior o inferior. Estos bolsillos se complementan, con tapas, vivos, charreteras, ojaletes o cierres.
        .row.justify-content-center.mb-5
          .col-lg-10
            p.mb-0.text-center.caja.temporal--rosa.p-2 Hay varios modelos de bolsillo de ojal, empezaremos por el ms sencillo.
        p.mb-5 Hay varios modelos de bolsillo de ojal, empezaremos por el ms sencillo.
        .row.justify-content-center.mb-5
          .col-lg-5
            figure
              img(src="@/assets/curso/tema2-imagen250.svg")
          .col-lg-7
            p.mb-4 Este bolsillo se elabora con tres piezas:
            ol.lista-ol--cuadro
              li
                .lista-ol--cuadro__vineta
                  span 1
                | Fondo de bolsillo
              li
                .lista-ol--cuadro__vineta
                  span 2
                | La vista
              li
                .lista-ol--cuadro__vineta
                  span 3
                | La pieza trasera del pantaln
        .row.justify-content-center.mb-3
          .col-auto
            figure
              img(src="@/assets/curso/tema2-imagen251.svg")
    figure.mb-4
      img(src="@/assets/curso/tema2-imagen252.svg")
      figcaption Fuente: Olivares, 2012.
    .row.justify-content-center.mb-4
      .col-lg-4
        figure
          img(src="@/assets/curso/tema2-imagen253.svg")
      .col-lg-8
        .h4.texto-rosa Bolsillo de fuelle
        p.mb-5 Este bolsillo es algo similar al bolsillo de parche, su diferencia radica en que tiene un pliegue en su parte principal, dndole mayor amplitud y un aspecto diferente.
        .row.justify-content-center
          .col-lg-8
            p.mb-0 Este bolsillo es algo similar al bolsillo de parche, su diferencia radica en que tiene un pliegue en su parte principal, dndole mayor amplitud y un aspecto diferente.
              br
              br
              | Para el trazo de este bolsillo se utiliza el bsico de parche, luego se da ventaja a lo que ser el fuelle en la parte central a cada lado marcando piquetes donde debe unirse. Este es uno de los modelos ms sencillos de este tipo de bolsillo y se acompaa con una tapa o tapeta.
          .col-lg-4
            figure
              img(src="@/assets/curso/tema2-imagen254.svg")
              figcaption Fuente: elaboracin propia en Optitex.
    .row.justify-content-center.mb-4
      .col-lg-10
        .cajon.color-primario.p-4.mb-4
          p.mb-0.text-center El volumen se da a partir de dobleces. Para trazar este bolsillo se parte de un rectngulo de 22 cm de ancho por 17 cm de largo y cada una de las separaciones para los dobleces son de 2 cm para formar el fuelle.
      .col-lg-7
        figure
          img(src="@/assets/curso/tema2-imagen255.svg")
          figcaption Fuente: elaboracin propia en Corel Draw.

    .h4.texto-rosa Cargo o utilitarios
    p.mb-5 Otra variacin es a partir de un fuelle alrededor de la pieza principal del bolsillo, la cual le da volumen formando una caja con altura, generalmente se emplean en los uniformes militares, de oficios especficos y deportivos, se emplean para cargar objetos voluminosos, y generalmente, llevan tapa. Estos se emplean en pantalones (en posteriores, laterales e incluso delanteros), chaquetas y accesorios (bolsos o maletas).

    .row.justify-content-center.mb-5
      .col-lg-7
        figure.mb-4
          img(src="@/assets/curso/tema2-imagen256.svg")
        p.mb-5 Para realizar este tipo de bolsillo, se toma la dimensin del borde del bolsillo (sin tener en cuenta la boca) se hace un rectngulo con esta dimensin de largo (se debe tener en cuenta que lleva las mismas ventajas de costura aplicadas a la boca en los extremos, ya que al dar la vuelta por el bolsillo stos constituyen parte de la boca) por el ancho o profundidad deseado ms las costuras.
        figure.mb-0
          img(src="@/assets/curso/tema2-imagen257.svg")
          figcaption Fuente: elaboracin propia en Optitex.
