      .col-lg-8
        .h4.texto-rosa Bolsillo a la cadera y/o de costado
        p.mb-0 Este tipo de bolsillo va integrado en la misma prenda, y funciona a travs de falsos y aberturas. Su trazo se encuentra en el pantaln base y en el bsico de <i>jean</i>.
          br
          br
          | Este tipo de bolsillo puede presentar variaciones en trminos de tamaos, formas, tipo de abertura o boca segn diseo.
