    .h4.texto-rosa Bolsillo insertado en costura o invisible
    p.mb-5 Este tipo de bolsillo va directamente sobre la costura lateral de la prenda. All se marcan piquetes a las 2 bolsas del forro formando el bolsillo. Queda desapercibido.
    .row.justify-content-center.mb-3
