    #t_2_11.titulo-segundo
      h2 2.11   Variaciones del pantaln

    figure.mb-5
      img(src="@/assets/curso/tema2-imagen264.svg")

    p.mb-5 Los estilos de los pantalones varan segn las temporadas, las lneas de vestir y los requerimientos del mercado. A continuacin, se presentan algunas de las variaciones ms comunes a realizar en los pantalones masculinos.

    .row.justify-content-center.align-items-center.caja-gris2
      .col-lg-5
        figure
          img(src="@/assets/curso/tema2-imagen265.svg")
          figcaption Fuente: elaboracin propia en Optitex.
      .col-lg-7
        .h4.texto-rosa Los diferentes largos de pantaln
        p.mb-0 El largo del pantaln vara segn el clima, la ocasin de uso y las preferencias del consumidor. El nombre de cada una de las siluetas (tipo de prenda segn el largo) puede variar dependiendo del lugar en que se resida.
          br
          br
          | En la siguiente grfica se muestran algunos de los largos ms conocidos.

    .row.justify-content-center
      .col-lg-11
        p.text-center Para el desarrollo de los patrones se parte del bsico de pantaln y se hacen las divisiones del largo de las perneras desde el tiro hasta el ruedo, as:
          br
          br
          | <i>hot pants</i> o pantalones calientes: en la pernera delantera, desde el tiro y por la entrepierna marcar 5 cm. Escuadrar con respecto a la lnea de aplome.
    .row.justify-content-center.mb-5
      .col-lg-6.col-md-6
        SlyderC.mb-5(:datos="datosSlyder15")
    .row.justify-content-center
      .col-lg-10
        .row.justify-content-center
          .col-lg-8
            .cajon.color-primario.p-4.mb-4
              p.mb-0.text-center Otras variaciones comunes son las que se hacen al volumen de las perneras, entre las que se encuentran:
            p.mb-0 Pantaln con prenses o pliegues: como su nombre lo indica, es un pantaln al que se le incorporan prenses o pliegues, puede llevar desde uno hasta tres o cuatro pliegues en cada una de las perneras delanteras. Estos pliegues se le adicionan con el fin de dar volumen al pantaln y as brindar comodidad.

          .col-lg-4
            figure
              img(src="@/assets/curso/tema2-imagen271.svg")
    .row.justify-content-center.mb-5
      .col-lg-6.col-md-6
        SlyderC.mb-5(:datos="datosSlyder16")

    .cajon.color-primario.p-4.mb-5
      p.mb-0.text-center Otra forma de hacer pliegues o prenses, es marcarlos hasta cierto punto en la pernera (o la prenda que se est trabajando) donde se quiere dar volumen. Para lograrlo, se marca en forma de te (T) o ye (Y) hacia los contornos cercanos. Para el ejemplo se trazar un solo prense hasta la altura de la rodilla y se llevar al costado y a la entrepierna:

    .row.justify-content-center.mb-5
      .col-lg-6
        figure
          img(src="@/assets/curso/tema2-imagen277.svg")
          figcaption Fuente: elaboracin propia en Optitex.
      .col-lg-6
        figure(style="border: 1px solid #C9447E")
          img.p-4(src="@/assets/curso/tema2-imagen278.svg")
          figcaption Fuente: elaboracin propia en Optitex.

    .row.justify-content-center.mb-5
      .col-lg-11
        .background1-especial2.p-2.mb-4
          p.mb-0.text-center Adicionar (pegar por detrs) el papel necesario para completar el prense, doblar, cortar (descargue) y marcar piquetes.
        .row.justify-content-center
          .col-lg-4
            figure
              img(src="@/assets/curso/tema2-imagen279.svg")
              figcaption Fuente: elaboracin propia en Optitex.
          .col-lg-8
            p.mb-4 Otra modificacin usual en los pantalones es el tipo de bota que presentan, a continuacin, dos de las ms comunes.
              br
              br
              | Bota ajustada o tubo: tambin conocida como Slim (por su nombre en ingls), es el tipo de bota que se ajusta al cuerpo.
            figure.mb-5
              img(src="@/assets/curso/tema2-imagen280.svg")
            p.mb-5 A diferencia de lo trabajado en los prenses, siempre que se necesite modificar la silueta de la bota de un pantaln, se deben trabajar ambas perneras, esto con el fin mantener el equilibrio en la prenda.
            .row.justify-content-center
              .col-lg-6
                p.mb-0 Para trabajar la bota ajustada se entra (reduce medida hacia el centro) en los puntos: rodilla por entrepierna, rodilla por costado, bota por entrepierna y bota por costado, cuidando siempre de dejar el contorno mnimo de rodilla (segn tabla de medidas) y el contorno mnimo de bota (para que pase el pe, por lo cual se mide el contorno pasando por el taln y el empeine del pie en posicin extendido).
              .col-lg-6
                figure
                  img(src="@/assets/curso/tema2-imagen281.svg")

    figure.mb-5
      img(src="@/assets/curso/tema2-imagen282.svg")

    .row.justify-content-center
      .col-lg-8
        .row.caja.temporal--rosa.align-items-center
          .col-lg-4
            figure
              img(src="@/assets/curso/tema2-imagen283.svg")
          .col-lg-8
            p.mb-4 Lo contrario a las botas ajustadas son las botas con volumen, al igual que las anteriores, el uso de estas depender de la temporada y la moda del momento.
            .h4.texto-rosa.mb-4 Bota campana o flare
            p.mb-0 Para dar el volumen necesario a la bota campana se utiliza el mismo procedimiento del prense hasta la rodilla, pero partiendo de la bota y llegando hasta el punto deseado (generalmente la rodilla), as:

