    #t_2_12.titulo-segundo
      h2 2.12   Desarrollo de modelos del pantaln

    .row.mb-5
      .col-lg-5
        figure
          img(src="@/assets/curso/tema2-imagen289.svg")
      .col-lg-7
        p.mb-0 En el campo laboral del patronista se conoce como desarrollo de modelos, el proceso de transformacin de los patrones bsicos en un diseo (o modelo) especfico, es decir, en el caso de los pantalones, a partir del molde bsico de pantaln realizar las transformaciones y adecuaciones necesarias para lograr el modelo entregado por el departamento de diseo. Las variaciones ms comunes de los pantalones son: la silueta, las bocas de los bolsillos, el largo, los bolsillos posteriores, adiciones de bolsillos, cortes o piezas.
          br
          br
          | A continuacin, se presentan tres modelos utilizados por las marcas de masculino.

    AcordionA.mb-5(tipo="b" clase-tarjeta="tarjeta tarjeta--gris")
      .row(titulo="Pantaln chino o Kakis (Caquis)").align-items-center
        .col-md-4.mb-4.mb-md-0
          figure
            img(src='@/assets/curso/tema2-imagen290.svg')
        .col-md-8
          p.mb-0 pantaln confeccionado generalmente en dril liviano o popelina pantalonera, de silueta recta y bota semiajustada, las bocas de bolsillo en diagonal y los bolsillos posteriores de ribete y con tapa.

      div(titulo="Jogger")
        .row.mb-4
          .col-lg-7
            figure
              img(src="@/assets/curso/tema2-imagen291.svg")
              figcaption Fuente: elaboracin propia en Optitex.
          .col-lg-5
            p.mb-0 Pantaln confeccionado generalmente en tejido de puntos tipo burda o perchado, con pretina completa (enresortada), imitacin de la jota (no lleva cremallera) y puos en los bajos. Se caracteriza por tener el tiro bajo y ser amplio. Como la cintura no lleva abertura debe trabajarse con la medida de cadera para que el usuario pueda vestirlo. Usualmente sus bocas de bolsillo son diagonales y lleva un solo bolsillo (de ribete o parche, segn el diseo) en la parte posterior. Puede o no llevar cotilla, algunos jogger llevan la cotilla curva.
        SlyderEspecial.mb-0(:datos="datosSlyder18")

      .row(titulo="Pantaln cargo").align-items-center
        .col-md-4.mb-4.mb-md-0
          figure
            img(src='@/assets/curso/tema2-imagen294.svg')
        .col-md-8
          p.mb-0 Tambin conocido como utilitario (utility en ingls), se caracteriza por tener mltiples bolsillos, especialmente un par de bolsillos laterales de parche tipo fuelle con tapa que van en los costados sobre la rodilla. Su silueta es usualmente recta o amplia y lleva el ruedo enresortado.
            br
            br
            | Actualmente se realizan variados diseos de pantalones para los hombres, para conseguir los patrones apropiados para sus desarrollos se hace necesario desarrollar las habilidades por medio de la prctica y siempre probarlos en la tela real en que se harn las prendas.

